function arrayConcat(array, newArray) {
  if (Array.isArray(array) && Array.isArray(newArray)) {
    let arrayBox = [...array]
    newArray.forEach((single, index) => {
      if (Number(single.time) > Number(array[array.length - 1].time)) {
        arrayBox.push(single)
      }
    })
    return arrayBox
  }
}
